.tabs{
  display: flex;
}

.tab{
  border-radius: 2.5em;
  height: 3.375em;
  padding: 0 1.75em;
  display: flex;
  align-items: center;
  position: relative;
  font-weight: 600;
  font-family: $font2;
  background-color: $gray;
  transition: all .3s;
  @include lastM(0 .875em 0 0);
  span{
    font-size: 1.25em;
    line-height: 120%;
  }
  &:hover{
    background: $yellow;
    cursor: pointer;
  }
  &._active{
    background: $yellow;
    pointer-events: none;
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}


@media screen and (max-width: 992px){
  .tab{
    padding: 0 1.75em;
    height: 3.125em;
    @include lastM(0 .375em 0 0);
    span{
      font-size: 1em;
    }
  }
}
