.partners{
  padding: 17.5em 0 9.25em;
  background-color: #fff;
  &__grid{
    padding: 4.875em 3.125em 0;
    display: grid;
    grid-template-columns: repeat(5,calc(20% - 1.5em / 5));
    gap: .375em;
  }
}


.partner-item{
  padding: 1em 2em;
  height: 5em;
  @include center;
  img{
    transition: all .3s;
    max-width: 100%;
    max-height: 100%;
    display: block;
  }
  @media screen and (min-width: 993px){
    &:not(:hover) img{
      filter: grayscale(100%);
      opacity: .5;
    }
  }
}

.partners-reviews{
  padding: 2em 2.75em;
  background: #fafafa;
  border-radius: 1.25em;
  display: grid;
  grid-template-columns: 8.3125em calc(100% - 8.3125em - 2.1875em);
  justify-content: space-between;
  margin: 2.25em auto 0;
  width: 54em;
  max-width: 100%;
  &__item{
    width: 100%;
    margin-bottom: .75em;
    display: block;
    box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.12);
    border-radius: .375em;
  }
  &__desc{
    line-height: 164%;
    color: #737373;
  }
  &__tags{
    margin-top: 1.25em;
    font-size: 1em;
    line-height: 164%;
    color: $primary;
    font-weight: 600;
    span{
      display: inline-block;
      white-space: nowrap;
      margin-right: .75em;
    }
  }
}


@media screen and (max-width: 992px){
  .partners{
    padding: 3.5em 0 4.75em;
    &__grid{
      padding: 1.6875em 0 0;
      grid-template-columns: repeat(5,calc(20% - .6em));
      gap: .75em;
    }
  }

  .partner-item{
    padding: 1em 2em;
    background: #efefef;
    border-radius: .75em;
    height: 3.8125em;
  }

  .partners-reviews{
    display: none;
  }
}


@media screen and (max-width: 576px){
  .partners{
    .container{
      padding: 0 1.375em;
    }
    &__grid{
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      gap: unset;
      width: calc(100% + .75em);
      margin-top: 1em;
      padding-top: 0;
    }
  }

  .partner-item{
    width: calc(50% - .75em);
    margin: .75em .75em 0 0;
    background-image: url(../img/arrow3.svg);
    background-repeat: no-repeat;
    background-size: .625em auto;
    background-position: right .625em top .625em;
  }
}
