.advantages{
  &__grid{
    display: grid;
    grid-template-columns: 20.625em calc(100% - 29.25em);
    justify-content: space-between;
  }
  &__img{
    width: 100%;
    display: block;
  }
  .tabs{
    margin: 1.5em 0 3em;
  }
  .tab{
    ._view2{
      display: none;
    }
  }
  &__desc{
    font-family: $font2;
    font-weight: 600;
    margin-bottom: 1.5em;
    color: #acacac;
    span{
      color: #505050;
    }
  }
}

.advantage-card{
  display: flex;
  align-items: flex-start;
  border: 6px solid #fff;
  border-radius: 2.5em;
  padding: 1.75em 2.25em 2em 1.625em;
  &__icon{
    width: 4.625em;
    height: 4.625em;
    flex-shrink: 0;
    margin-right: 1.875em;
    img{
      width: 100%;
      display: block;
      box-shadow: 0 .25em .75em rgba(0, 0, 0, 0.07);
      border-radius: 50%;
    }
  }
  &__content{
    @include grid100gap(.875em);
  }
  &__desc{
    line-height: 164%;
    color: #737373;
  }
}

.swiper-scrollbar{
  height: .625em;
  background-color: #ececec;
  margin-top: 2.125em;
  &-drag{
    background-color: $yellow;
  }
}

.advantages-slider{
  @media screen and (min-width: 577px){
    .swiper{
      &-wrapper{
        transform: unset !important;
        @include grid100gap(.8125em);
      }
      &-slide{
        width: 100%;
      }
      &-scrollbar{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .advantages{
    padding: 2.25em 0;
    &__grid{
      grid-template-columns: 100%;
    }
    &__col{
      display: none;
    }
    .tabs{
      margin: .875em 0 1.75em;
    }
  }
}


@media screen and (max-width: 576px){
  .advantages{
    overflow: hidden;
    .tab{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .advantages-slider{
    .swiper{
      &-wrapper{
        align-items: stretch;
      }
      &-slide{
        width: 20.25em;
        height: auto;
        padding-right: .625em;
      }
    }
  }

  .advantage-card{
    position: relative;
    border-width: .25em;
    padding: 1em;
    height: 100%;
    &__icon{
      position: absolute;
      top: 1em;
      left: 1em;
      width: 3.0625em;
    }
    &__top{
      display: flex;
      align-items: center;
      min-height: 3.0625em;
      padding-left: 3.8125em;
    }
    &__title{
      font-size: 1em;
    }
    &__desc{
      font-size: .875em;
      line-height: 151%;
    }
  }
}
