.about{
  &__inner{
    padding: 2.25em 0 28em;
    position: relative;
  }
}

.about-info{
  display: flex;
  align-items: flex-start;
  &__icon{
    width: 3.4375em;
    flex-shrink: 0;
    margin-right: .6875em;
    margin-top: 1.0625em;
  }
  &__text{
    max-width: 30.5em;
  }
  &__desc{
    margin-top: 1.6875em;
    color: #acacac;
    font-weight: 600;
    font-family: $font2;
  }
}

.about-slider{
  margin-top: 12.5em;
  @media screen and (min-width: 577px){
    .swiper{
      &-wrapper{
        transform: unset !important;
        display: grid;
        grid-template-columns: repeat(2,calc(50% - 4.2em));
        gap: 8.4em;
      }
      &-slide{
        width: 100%;
        &:nth-child(even){
          transform: translateY(-9.375em);
        }
      }
      &-pagination{
        display: none;
      }
    }
  }
}

.about-card{
  padding: 6.75em 0 0 8.125em;
  position: relative;
  &__media{
    position: absolute;
    top: 0;
    left: 0;
    background-image: url(../img/ellipse.svg);
    @include bgImgSize(100%);
    width: 17.375em;
    height: 17.375em;
  }
  &__icon{
    position: absolute;
    top: 0;
    left: -4em;
    height: 7.5em;
    width: 12.5em;
    img{
      position: absolute;
      left: 0;
      bottom: 0;
      max-width: 20.625em;
      max-height: 15.5em;
    }
  }
  &__title,
  &__desc{
    position: relative;
    z-index: 2;
  }
  &__title{
    color: #686868;
    font-size: 1.5em;
    line-height: 121%;
    font-weight: 700;
    font-family: $font2;
    span{
      color: $primary;
    }
  }
  &__desc{
    margin-top: 1em;
    font-size: 1em;
    line-height: 150%;
    color: #6d6d6d;
  }
}

.about-service{
  position: absolute;
  width: 100%;
  bottom: -5em;
  left: 0;
  border-radius: 2.5em;
  background-color: $yellow;
  display: grid;
  grid-template-columns: calc(100% - 43.125em) 43.125em;
  &__col1{
    padding: 5em 3.75em;
    background-image: url(../img/about-bg.webp);
    background-size: cover;
    border-radius: 2.5em 0 0 2.5em;
    position: relative;
  }
  &__col2{
    padding: 3.125em 4em 3.125em 4.75em;
  }
  &__img{
    position: absolute;
    width: 21.5625em;
    left: -7.5em;
    bottom: -8.875em;
  }
  &__title2{
    font-weight: 600;
    font-size: 5.0625em;
    line-height: 93%;
    mix-blend-mode: overlay;
  }
  &__title{
    line-height: 164%;
  }
  &__desc{
    font-size: 1.125em;
    line-height: 167%;
    margin-top: .94em;
  }
  &__nav{
    display: flex;
    margin-top: 1.0625em;
  }
  &__link{
    color: rgba(0, 0, 0, 0.8);
    font-weight: 500;
    display: flex;
    align-items: center;
    span{
      font-size: 1em;
      line-height: 164%;
    }
    svg{
      width: .4375em;
      margin-left: .75em;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .about{
    &__inner{
      padding: 1.875em 0 4.625em;
    }
  }

  .about-info{
    &__desc{
      margin-top: 1em;
    }
  }

  .about-slider{
    @media screen and (min-width: 577px){
      font-size: .625em;
      padding: 0 2em;
      margin-top: 15em;
      .swiper{
        &-wrapper{
          grid-template-columns: repeat(2,calc(50% - 2em));
          gap: 4em;
        }
      }
    }
  }

  .about-service{
    margin-top: 4.0625em;
    transform: unset;
    position: static;
    grid-template-columns: 19em calc(100% - 19em);
    &__col1{
      padding: 4em 3em;
    }
    &__col2{
      padding: 2em 1em;
    }
    &__img{
      width: 11.9375em;
      left: -1.5em;
      bottom: -4.25em;
    }
    &__title2{
      font-size: 3.125em;
    }
    &__desc{
      font-size: .875em;
      line-height: 164%;
      margin-top: 1.28em;
    }
  }
}


@media screen and (max-width: 576px){
  .about{
    overflow: hidden;
  }

  .about-info{
    &__icon{
      width: 2.375em;
      margin-top: .75em;
    }
  }

  .about-slider{
    margin-top: 4.625em;
    .swiper{
      &-slide{
        transition: all .3s;
        &:not(.swiper-slide-active){
          opacity: 0;
          pointer-events: unset;
        }
      }
      &-pagination{
        position: static;
        margin-top: 1.875em;
      }
    }
  }

  .about-card{
    padding: 0;
    &__top{
      padding-left: 6.4375em;
      min-height: 6.875em;
      display: flex;
      align-items: center;
    }
    &__title{
      font-size: 1.125em;
    }
    &__media{
      height: 9.625em;
      width: 9.625em;
      background-image: url(../img/arrow2.svg);
      top: -1.875em;
      left: -5.3125em;
    }
    &__icon{
      left: 4.75em;
      top: 1em;
      width: 6.8125em;
      height: 7.125em;
      img{
        max-width: 100%;
        max-height: 100%;
        position: static;
        display: block;
      }
    }
    &__desc{
      font-size: .875em;
      margin-top: 2.35em;
      padding: 0 0 0 2.21em;
    }
  }

  .about-service{
    grid-template-columns: 100%;
    &__col1{
      border-radius: 2.5em 2.5em 0 0;
      padding-right: 3.5em;
      padding-bottom: 6.25em;
    }
    &__col2{
      padding: 4.75em 1em 4.125em;
    }
  }
}
