.app{
  padding: 5em 0 5.375em;
  &__grid{
    display: grid;
    grid-template-columns: 21.625em calc(100% - 26.75em);
    justify-content: space-between;
    gap: 2.875em;
    margin-top: 3.3125em;
  }
  &__img{
    width: 100%;
    display: block;
  }
  &__col{
    padding-top: 2.5em;
  }
  .app-stores{
    margin: 4.625em auto 0;
    width: 59.0625em;
    max-width: 100%;
    display: grid;
    grid-template-columns: repeat(6,calc(100% / 6 - 7.5em / 6));
    gap: 1.5em;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 1.1875em;
  }
  &__link{
    color: $primary;
    text-decoration: underline;
    font-family: $font1;
  }
}

.app-slider{
  height: 31.25em;
  .swiper{
    &-container{
      position: relative;
      height: 100%;
      overflow: hidden;
      padding-right: 2em;
      &:after{
        content: '';
        position: absolute;
        height: 5em;
        width: 40em;
        max-width: calc(100% - 2em);
        left: 0;
        bottom: 0;
        background-image: linear-gradient(to top, rgba(#F6F6F6, 1), rgba(#F6F6F6, 0));
        z-index: 2;
      }
    }
    &-slide{
      padding-bottom: 1.625em;
      height: auto;
    }
    &-scrollbar{
      right: .75em;
      width: .3125em;
      margin: 0;
      &-drag{
        &:after{
          content: '';
          position: absolute;
          left: 50%;
          bottom: 0;
          transform: translateX(-50%);
          width: 1.75em;
          height: 1.75em;
          background-color: $yellow;
          border-radius: 50%;
        }
      }
    }
  }
}

.app-item{
  max-width: 40em;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  .m-badge{
    margin-bottom: 1em;
  }
  &__text{
    font-size: 1em;
    line-height: 146%;
    color: #737373;
  }
}

.m-badge{
  @include center;
  padding: .4375em 1em;
  min-height: 2.25em;
  border-radius: .375em;
  background-color: $yellow;
  font-weight: 500;
  &--black{
    background: linear-gradient(161deg, #000 0%, #5b5b5b 100%);
    color: #fff;
  }
}


@media screen and (max-width: 992px){
  .app{
    padding: 2.25em 0 3.375em;
    &__grid{
      grid-template-columns: 11.75em calc(100% - 14.625em);
      margin-top: 2.5em;
    }
    &__col{
      padding-top: 1em;
    }
    .app-stores{
      margin-top: 2.75em;
      width: 20.625em;
      grid-template-columns: repeat(3,calc(100% / 3 - 2.25em / 3));
      gap: .5625em 1.125em;
    }
    &__nav{
      margin-top: 2.75em;
    }
  }

  .m-badge{
    .text18{
      font-size: 1em;
    }
  }

  .app-slider{
    .swiper{
      &-slide{
        padding-bottom: 1.375em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .app{
    &__grid{
      grid-template-columns: 100%;
    }
    &__col{
      padding-top: 0;
      width: 11.75em;
      margin: 0 auto;
      transform: translateX(.375em);
    }
  }

  .app-slider{
    .swiper{
      &-container{
        padding-right: 0;
        &:after{
          max-width: 100%;
        }
      }
      &-scrollbar{
        display: none;
      }
    }
  }

  .app-item{
    align-items: center;
    text-align: center;
    .m-badge{
      max-width: 16.9375em;
    }
  }
}
