.m-section{
  padding: 3.5em 0;
}

.main{
  overflow: hidden;
}

.main-slider{
  overflow: hidden;
  border-radius: 2.5em;
  position: relative;
  .swiper{
    &-container{
      width: calc(100% + 1em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      padding-right: 1em;
    }
    &-button{
      position: absolute;
      width: 11.625em;
      height: 11.625em;
      box-shadow: 0 4px 68px 0 rgba(0, 0, 0, 0.25);
      background-color: #fff;
      display: flex;
      align-items: unset;
      justify-content: unset;
      border-radius: 50%;
      padding: 3.25em;
      right: -3.1875em;
      bottom: -3.75em;
      top: unset;
      margin: 0;
      svg{
        width: 3.0625em;
        height: 1.5em;
      }
    }
    &-pagination{
      display: none;
    }
  }
}

.main-banner{
  background-color: $yellow;
  border-radius: 2.5em;
  height: 100%;
  min-height: 23em;
  position: relative;
  overflow: hidden;
  padding: 2.75em 6.25em 2.5em 25em;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 2.5em;
    }
    ._view2{
      display: none;
    }
  }
  &__content{
    position: relative;
    z-index: 2;
  }
  &__top{
    position: relative;
  }
  &__arrow{
    position: absolute;
    right: 10.75em;
    top: 100%;
    margin-top: 1em;
    width: 7.5625em;
  }
  .h1{
    span{
      color: $primary;
    }
  }
  &__desc{
    margin-top: 1.375em;
  }
  .app-stores{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
    gap: .8125em 1.5em;
    max-width: 28.75em;
  }
  &__pizza{
    display: none;
  }
  @media screen and (min-width: 577px){
    &._type3{
      padding: 6em 12.25em 6em 32.4375em;
    }
    &._type4{
      padding: 6em 7.5em 6em 25em;
    }
  }
}

.app-stores{
  &__item{
    background-color: $black;
    display: flex;
    align-items: center;
    height: 2.5em;
    padding: 0 .8125em;
    border-radius: .25em;
    &:hover{
      background-color: lighten($black,10%);
    }
    img{
      max-width: 100%;
      max-height: 1.4375em;
    }
  }
}

.swiper-pagination{
  @include center;
  &-bullet{
    width: 1em;
    height: 1em;
    opacity: 1;
    background-color: #ebebeb;
    margin: 0 .375em !important;
    &-active{
      background-color: $yellow;
    }
  }
  &--red{
    .swiper-pagination-bullet-active{
      background-color: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 1.625em 0 1.25em;
  }

  .main-slider{
    font-size: .625em;
    @media screen and (min-width: 577px){
      .h1{
        font-size: 2.5em;
      }
    }
  }

  .app-stores{
    &__item{
      height: 1.75em;
      padding: 0 .5625em;
      img{
        max-height: 1em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .main-slider{
    overflow: unset;
    font-size: 1em;
    .swiper{
      &-slide{
        transition: all .3s;
        &:not(.swiper-slide-active){
          opacity: 0;
          pointer-events: unset;
        }
      }
      &-button{
        display: none;
      }
      &-pagination{
        display: flex;
        position: static;
        margin-top: 1.875em;
      }
    }
  }

  .main-banner{
    min-height: 33.125em;
    padding: 2.5em 2em;
    overflow: unset;
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__arrow,
    .app-stores{
      display: none;
    }
    &__desc{
      font-weight: 600;
      margin-top: 1em;
    }
    &__pizza{
      display: block;
      position: absolute;
      width: 20.9375em;
      right: -7.5em;
      top: 7.5em;
    }
    &._type4{
      overflow: hidden;
      .main-banner__img{
        top: .125em;
        left: -.25em;
        width: calc(100% + .25em);
        //background-color: $yellow;
      }
    }
  }
}
